import logo from "./icons/logo.png"
import axios from "axios";
import React from "react";
import { Image, Row, Col, Divider, Slider, Button, Input, Select, Space, Progress, Typography, List, Popconfirm, message, Modal, notification } from 'antd';
const { Title } = Typography;

class ProjectLists extends React.Component {
    constructor(props) {
        super(props)


        var date = new Date(this.props.info.createdAt);

        // 
        var year = date.getFullYear();
        var month = ("0" + (date.getMonth() + 1)).slice(-2);
        var day = ("0" + date.getDate()).slice(-2);
        var hour = ("0" + date.getHours()).slice(-2);
        var minute = ("0" + date.getMinutes()).slice(-2);
        var second = ("0" + date.getSeconds()).slice(-2);

        // 
        var outputTime = year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + second;
        this.state = { date: outputTime }


    }


    render() {
        console.log(this.props.info)
        return (
            <div style={{ margin: "0px", padding: "0px" }}>
                <Row style={{ margin: "0px", padding: "0px" }} justify="space-around" align="middle" >
                    <Col span={22} style={{ margin: "0px", padding: "0px" }}>
                        <Row>
                            <Col span={24}>
                                <Row>
                                    <Col span={2}></Col>
                                    <Col span={15}> <Title style={{ margin: "0", overflow: "hidden", textOverflow: "ellipsis" }} level={5}><div style={{ margin: "0", overflow: "hidden", textOverflow: "ellipsis",whiteSpace:"nowrap"  }}>{this.props.info.name}</div></Title> </Col>
                                    
                                    {!this.props.running ? <Col span={5}><Typography style={{ margin: "0", textAlign: "left", color: "red" }} ></Typography></Col> : <Col span={5}></Col>}
                                
                                    <Col span={2}></Col>
                                </Row>

                            </Col>
                            <Col span={24}>
                                <Row>
                                    <Col span={1}></Col>
                                    <Col span={12}> <Typography style={{ margin: "0", textAlign: "left", color: "#9A9A9A" }} >{this.state.date}</Typography> </Col>
                                    <Col span={2}></Col>
                                    <Col span={9}><Typography style={{ margin: "0", textAlign: "left", color: "#9A9A9A" }} >{this.props.info.diskUsage}</Typography> </Col>
                                </Row></Col>
                        </Row>

                    </Col>
                    <Col span={2}> <Button style={{ margin: "0px", padding: "0px" }} size="small" onClick={this.showModal} type="link" danger ></Button></Col>
                </Row>
                <Divider style={{ margin: "0", padding: "0" }} orientation="left"></Divider>


            </div>
        )
    }
}


class projects extends React.Component {

    constructor(props) {
        super(props)
        this.state = { visible: false, newProjectName: "", projectsList: [] }
    }
    componentDidMount() {
        fetch("http://raspberrypi:9999/api/project")
            .then(res => res.json())
            .then(json => {

                this.setState({
                    projectsList: json.data
                })
            })

    }
    componentDidUpdate() {


    }

    openNotification = () => {

    };
    jumpToHome() {

        window.location.href = window.location.origin + '/#/Home';

    }

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {

        this.setState({
            visible: false,
        });

        let projectdata = {
            name: this.state.newProjectName,
            info: "test-info",
            interval: 3000,
            video: {
                enable: true,
                fps: 2,
                maxImage: 10
            }

        }
        console.log(projectdata)

        axios({
            method: 'POST', // 
            url: 'http://raspberrypi:9999/api/project', //  url
            data: projectdata
        }).then(response => {
            console.log(response.data)
            
            window.location.href = window.location.origin + '/#/ParamSetting/'+response.data.data.name;
        }).catch((response) => {
            console.log(this.props.info)
       
            notification.open({
                message: '',
                description:
                    '',
                onClick: () => {

                },
            });
            console.log(response);
        });
    };

    handleCancel = e => {

        this.setState({
            visible: false,
        });
    };

    changeProjectName = (value) => {
        this.setState({
            newProjectName: value.target.value,
        });
    };

    render() {

        return (
            <div>

                <Modal
                    title=""
                    open={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <Row>
                        <Col span={2}></Col>
                        <Col span={20}></Col>
                        <Col span={2}></Col>
                        <Col span={2}></Col>
                        <Col span={20}><Input placeholder="" onChange={this.changeProjectName} /></Col>
                        <Col span={2}></Col>
                    </Row>


                </Modal>
                <Row style={{ position: "fixed", zIndex: "1", top: "0px", left: "0px", width: "100%" }}>
                    <Col span={24}>
                        <div style={{ backgroundColor: "#000000", padding: "1%", width: "100%" }}>
                            <Row justify="space-around" align="middle" >
                                <Col span={2} style={{ display: 'flex' }} onClick={this.jumpToHome} >
                                    <Row justify="space-around" align="middle"  >
                                        <Col span={24} style={{ display: 'flex' }}>
                                            <div style={{ width: "80%" }}>
                                                <img src={logo} style={{ width: "100%", height: "100%", display: "flex" }} />
                                            </div>
                                        </Col>
                                    </Row>
                                </Col>
                                <Col span={20}><Typography style={{ textAlign: "left", color: "#FFFFFF" }}><b></b></Typography></Col>
                                <Col span={2}></Col>
                            </Row>
                        </div>
                    </Col>
                </Row>


                <Row style={{ display: 'flex', marginTop: "12%", marginBottom: "0%" }} >
                    <Col span={19}> </Col>
                    <Col span={4}><Button style={{ color: "#00C2FF" }} size="small" onClick={this.showModal} ></Button></Col>
                    <Col span={1}> </Col>

                </Row>

                <Divider style={{ margin: "0", padding: "0" }} orientation="left"></Divider>


                <div style={{ margin: "0px", padding: "0px" }}>
                    {this.state.projectsList.map(projectsList =>
                        <div style={{ margin: "0px", padding: "0px" }} key={projectsList.name}>
                            <ProjectLists info={projectsList}></ProjectLists>
                        </div>)}
                </div>


            </div>
        )
    }
}

export default projects;